import { useState } from 'react';
import './style.css'

import LoadingBar from '../loadingBar/loadingBar';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowLeft, faCircle } from '@fortawesome/free-solid-svg-icons';

export default function ComplexLoading({data, label="", min=0, max=100, showPercent, showPercentChildren, parentDescription }) {
    const [openComplex, setOpenComplex] = useState(false);
    const [descriptionText, setDescriptionText] = useState({text: parentDescription, name: null});

    var total = data.map(item => ((item.value - min) / (max - min)) / data.length * 100)
    total = total.reduce((a, b) => a + b, 0)

    const onOpenComplex = () => {
        if(openComplex) onSetParentDescription()
        setOpenComplex(!openComplex)
    }
    const onSetDescriptionText = (index) => {
        if(descriptionText.name === data[index].name) {
            onSetParentDescription()
        } else if(data[index].description) setDescriptionText({text: data[index].description, name: data[index].name})
    }
    const onSetParentDescription = () => setDescriptionText({text: parentDescription, name: null})

    return (
        <div className={openComplex ? "open complex-loading" : "complex-loading"}>
            <div className="complex-header">
                <div className="complex-label"><h4>{label}</h4></div>
                <div className="complex-description">
                    { descriptionText.text !== parentDescription && (
                        <div className="complex-loading-btn-back">
                            <FontAwesomeIcon onClick={onSetParentDescription} className="btn" icon={faArrowLeft} />
                            <h4>{descriptionText.name}</h4>
                        </div>
                    ) }
                    {descriptionText.text}
                    { data.length > 1 && (
                        <div className="complex-loading-btn">
                            <button onClick={onOpenComplex} className="btn">{openComplex ? "Encolher" : "Ver Mais"}</button>
                        </div>
                    ) }
                </div>
            </div>
            <div className="complex-loading-bar">
                <div className="complex-loading-total">
                    <LoadingBar showPercent={showPercent} value={total} />
                </div>
                <ul className="complex-loading-inner">
                    {data.map( ({value, name, description}, index) => (
                        <li
                            key={index}
                            onClick={ () => onSetDescriptionText(index) }
                            className={descriptionText === description ?
                                "complex-loading-inner-item active" :
                                "complex-loading-inner-item"
                            }
                        >
                            <div className="complex-loading-inner-item-icon">
                                <FontAwesomeIcon icon={faCircle} />
                            </div>
                            <div className="complex-loading-inner-item-bar">
                                {name}
                                <LoadingBar value={value} showPercent={showPercentChildren} description={description}/>
                            </div>
                        </li>
                    )) }
                </ul>
            </div>
        </div>
    )
}