import "./style.css"

export default function LoadingBar({min = 0, max = 100, value = 0, showPercent = false}) {
    const valuePercent = (value - min) / (max - min) * 100;
    var width = valuePercent + "%";

    return (
        <div className="loading-bar">
            {value > 0 && (
                <div className="loading-bar-inner" style={{width}}>
                    {showPercent && 
                        <span className={valuePercent <= 18 ? "loading-bar-percent outside" : "loading-bar-percent"}>
                            {value === 100 ? value : value.toFixed(2)}%
                        </span>
                    }
                </div>
            )}
        </div>
    );

}